@import "src/assets/colors";
@import "src/assets/mixins";

.title {
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: $secondary;
  margin-bottom: 8px;
  @include title();
}
.darkTitle{
  color:$white;
}
.input {
  width: 544px;
  height: 56px;
  border: none;
  padding: 18px 20px;
  box-sizing: border-box;
  border: 2px solid transparent;
  &:focus, &:hover {
    outline: none;
    border: 2px solid $medium;
  }
}
.disabled{
  background: $light;
  border-radius: 2px;
  cursor: not-allowed;
}

.valid{
  border: 2px solid $error;
  border-radius: 2px;
}
.validText{
  color: $error;
}