@import "src/assets/colors";

.backHome {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: $secondary;
  margin-bottom: 32px;
  text-decoration: none;
  &:hover{
    text-decoration: underline;
    cursor: pointer;
  }
}
.backHomeDark{
  color: $white;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.title {
  text-align: start;
  margin-bottom: 72px;
  margin-top: 32px;

}
